const BASE_URL = 'http://127.0.0.1/api'
export const myRequest = (options)=>{
  return new Promise((resolve,reject)=>{
    uni.request({
      url:BASE_URL+options.url,// ,options
      method:options.method || 'GET',// optionsmethodGET
      data:options.data ||{},
      header:{
        'content-type': 'application/x-www-form-urlencoded' ,
      },// dataoptionsdata 
      success: (res) => {
        resolve(res.data)
      },
      fail: (err) => {
        uni.showToast({
          title:''
        })
        reject(err)
      }
    })
  })
}